import config from "../config";
import { MemoryHistory } from "../containers/memories/memoryHelper";
import authHelper from "../utils/authHelper";

class MemoryProvider {
  async getMemories(): Promise<MemoryHistory> {
    const token = authHelper.getToken();
    if (token === null) throw new Error("UNAUTHORIZED");
    const res = await fetch(`${config("serverPath")}/api/memories`, {
      method: "GET",
      headers: {
        Authorization: token
      }
    });
    if (!res.ok) throw new Error("Failed to get memories");
    return res.json();
  }
  async updateMemories(memoryHistory: MemoryHistory): Promise<MemoryHistory> {
    const token = authHelper.getToken();
    if (token === null) throw new Error("UNAUTHORIZED");
    const res = await fetch(`${config("serverPath")}/api/memories`, {
      method: "POST",
      body: JSON.stringify(memoryHistory),
      headers: {
        Authorization: token,
        "Content-Type": "application/json",
      }
    });
    if (!res.ok) throw new Error("Failed to update memories");
    return res.json();
  }
}

const memoryProvider = new MemoryProvider();

export default memoryProvider;
export { MemoryProvider };
