import { Memory, MemoryHistory } from "../containers/memories/memoryHelper";
import storageHelper from "./storageHelper";


export interface LocalDB {
  memoryHistory: MemoryHistory;
  appInfo: {
    message: string;
  }
};

let localDB: LocalDB | null = null;

const listeners: (() => void)[] = [];
export const addNavigationStateListener = (listener: () => void) => {
  listeners.push(listener);
  const remove = () => {
    const idx = listeners.indexOf(listener);
    if (idx === -1) return;
    listeners.splice(idx, 1);
  };
  return remove;
}

export const getDefaultDB = (): LocalDB => ({
  memoryHistory: {
    lastUpdated: 0,
    serverVersion: 0,
    memories: [],
  },
  appInfo: {
    message: '',
  },
});

export const getLocalDB = (): LocalDB => {
  if (localDB) return localDB;

  const storageMemory = (storageHelper.get('localDB') || {});
  // migration
  if (Array.isArray(storageMemory.memoryHistory)) {
    storageMemory.memoryHistory = {
      memories: storageMemory.memoryHistory,
    };
  }
  // end migration
  (window as any)._debugArtemLocalDB = localDB = {
    ...getDefaultDB(),
    ...storageMemory,
  };

  if (!localDB) throw new Error();

  triggerSaveCheck();
  return localDB;
}
export const saveLocalDB = () => {
  console.log('saving local db');
  if (localDB) {
    localDB.memoryHistory.lastUpdated = Math.max(
      localDB.memoryHistory.memories.reduce((acc, cur) => Math.max(acc, cur.modificationTime), 0),
      localDB.memoryHistory.serverVersion,
    );
  }
  storageHelper.set('localDB', localDB);
}

let lastDB = '';
let timeout: NodeJS.Timeout | null = null;
const triggerSaveCheck = () => {
  if (timeout) clearTimeout(timeout);
  let curDB = JSON.stringify(localDB);
  if (!lastDB) lastDB = curDB;
  else if (curDB !== lastDB) {
    saveLocalDB();
  } else {
    console.log('no changes');
  }
  timeout = setTimeout(triggerSaveCheck, 5_000);
}
