import React, { useCallback, useEffect, useState } from "react";
import Header from "../../components/header/header";
import styles from "./gallery.module.css";
import galleryProvider from "../../providers/localGalleryProvider";
import { DelayedRender } from "../../components/delayedRender/DelayedRender";
import Input from "../../components/input/input";

const PAGE_SIZE = 25;

interface Media {
  thumbnail: string;
  media: string;
  date: number;
}

export const Gallery: React.FC = () => {
  const [images, setImages] = useState<Media[]>([]);
  const [offset, setOffset] = useState(0);

  const [dateFilter, onChangeDateFilter] = useState('');
  const [tagFilter, onChangeTagFilter] = useState('');

  useEffect(() => {
    const getImages = async () => {
      const images = await galleryProvider.listImages({ offset, limit: PAGE_SIZE, tags: tagFilter, startDate: dateFilter }) as any[];
      setImages(old => [...old, ...images]);
    }
    getImages();
  }, [offset]);

  const reachedEnd = useCallback((visible: any) => {
    if (visible && offset <= images.length) setOffset(offset + PAGE_SIZE);
  }, [offset, images]);

  let lastDate = '';
  const getDate = (image: Media) => new Date(image.date).toLocaleDateString();

  return (
    <React.Fragment>
      <Header />
      Date:<Input value={dateFilter} onChange={onChangeDateFilter} style={{ width: 300 }} /><br />
      Tags:<Input value={tagFilter} onChange={onChangeTagFilter} style={{ width: 300 }} />
      <div className={styles.imageContainer}>
        {(images || []).map(image => {
          const imgDate = getDate(image);
          let dateHeader = null as any;
          if (imgDate !== lastDate) {
            dateHeader = <h2>{imgDate}</h2>;
            lastDate = imgDate;
          }
          return (
            <>
              {dateHeader}
              <a href={image.media} target="_blank" id={image.media}>
                <img className={styles.image} alt="img" src={image.thumbnail} />
              </a>
            </>)
        })}
        <DelayedRender key={images.length} unmountOnHidden={true} width={1} height={1} callback={reachedEnd}></DelayedRender>
      </div>
    </React.Fragment >
  );
};

export default Gallery;
