import config from "../config";
import authHelper from "../utils/authHelper";

class PushNotificationProvider {
  async getKey(): Promise<string> {
    const token = authHelper.getToken();
    if (token === null) throw new Error("UNAUTHORIZED");
    const res = await fetch(`${config("serverPath")}/api/push_subscription/keys`, {
      method: "GET",
      headers: {
        Authorization: token
      }
    });
    if (!res.ok) throw new Error("Failed to get keys");
    const json = await res.json();
    return json.publicKey;
  }
  async createPushSubscription(subscription: PushSubscription): Promise<PushSubscription> {
    const token = authHelper.getToken();
    if (token === null) throw new Error("UNAUTHORIZED");
    const res = await fetch(`${config("serverPath")}/api/push_subscription/register`, {
      method: "POST",
      body: JSON.stringify(subscription),
      headers: {
        Authorization: token,
        "Content-Type": "application/json",
      }
    });
    if (!res.ok) throw new Error("Failed to create subscription");
    return res.json();
  }
}

const pushNotificationProvider = new PushNotificationProvider();

export default pushNotificationProvider;
export { PushNotificationProvider };
