import React, { useRef, useState } from "react";
import Header from "../../components/header/header";
import style from "./memories.module.css";
import { useAsync } from "../../utils/useAsync";
import { getLocalDB, saveLocalDB } from "../../utils/database";
import Button from "../../components/button/button";
import { useReRender } from "../../hooks/useReRender";
import Input from "../../components/input/input";
import { Memory, addGeoToMemory, createNewMemory, deleteMemory, getGeolocation, getUniqueId, saveMemory } from "./memoryHelper";
import memoryProvider from "../../providers/memoryProvider";
import { calcCrow } from "./latLon";


interface MemoriesProps { }

interface MemoryFormProps {
  memory: Memory;
  onSave: { (memory: Memory): unknown };
  onCancel: { (): unknown };
  onDelete: { (): unknown };
}

export const MemoryForm = (props: MemoryFormProps) => {
  const { memory, onSave, onCancel, onDelete } = props;
  const [memoryForm, setMemoryForm] = useState(memory);

  const [isFetching, setIsFetching] = useState(false);
  if (!memoryForm.geoLocation && !memoryForm.id && !isFetching) {
    setIsFetching(true);
    getGeolocation().then(p => {
      setMemoryForm(f => ({ ...f, geoLocation: p }));
      setIsFetching(false);
    });
  }

  const timeDiff = useRef(0);
  if (typeof memoryForm.started.time === 'number' && typeof memoryForm.ended.time === 'number') timeDiff.current = memoryForm.ended.time - memoryForm.started.time;

  let positionString = '';
  let geoJson = '';
  try {
    geoJson = JSON.stringify(memoryForm.geoLocation, null, 4);
    if (memoryForm.geoLocation) positionString = `${memoryForm.geoLocation.coords.latitude},${memoryForm.geoLocation.coords.longitude}`;
  } catch (e) {
    geoJson = memoryForm.geoLocation as any;
  }

  const hasError = typeof memoryForm.started.time !== 'number' ||
    typeof memoryForm.ended.time !== 'number' ||
    typeof memoryForm.geoLocation === 'string';

  return (
    <div className={style.formContainer}>
      <p className={style.formFieldName}>Title</p>
      <Input value={memoryForm.title} onChange={v => setMemoryForm(f => ({ ...f, title: v }))} style={{ width: '100%' }} />
      <p className={style.formFieldName}>Description</p>
      <Input value={memoryForm.description} onChange={v => setMemoryForm(f => ({ ...f, description: v }))} isTextArea style={{ width: '100%', height: 100 }} />
      <p className={style.formFieldName}>Url</p>
      <Input value={memoryForm.url} onChange={v => setMemoryForm(f => ({ ...f, url: v }))} style={{ width: '100%' }} />
      <p className={style.formFieldName}>Tags</p>
      <Input value={memoryForm.tags.join(', ')} onChange={v => setMemoryForm(f => ({ ...f, tags: v.split(',').map(t => t.trim()) }))} style={{ width: '100%' }} />
      <p className={style.formFieldName}>Started Time</p>
      <Input
        value={typeof memoryForm.started.time === 'number' ? new Date(memoryForm.started.time).toLocaleString() : memoryForm.started.time}
        onChange={v => {
          const parsed = new Date(v);
          const time = parsed.getTime();
          const isValid = !isNaN(time);
          setMemoryForm(f => ({
            ...f,
            started: { ...f.started, time: isValid ? time : v as any },
            ended: { ...f.ended, time: isValid ? time + timeDiff.current : f.ended.time }
          }));
        }}
        style={{ width: '100%', border: typeof memoryForm.started.time !== 'number' ? '3px solid red' : undefined }} />
      <p className={style.formFieldName}>Ended Time</p>
      <Input
        value={typeof memoryForm.ended.time === 'number' ? new Date(memoryForm.ended.time).toLocaleString() : memoryForm.ended.time}
        onChange={v => {
          const parsed = new Date(v);
          const time = parsed.getTime();
          const isValid = !isNaN(time);
          setMemoryForm(f => ({
            ...f,
            ended: { ...f.ended, time: isValid ? time : v as any },
          }));
        }}
        style={{ width: '100%', border: typeof memoryForm.ended.time !== 'number' ? '3px solid red' : undefined }} />
      <p className={style.formFieldName}>Timezone</p>
      <Input
        value={memoryForm.started.timeZone}
        onChange={v => setMemoryForm(f => ({ ...f, started: { ...f.started, timeZone: v }, ended: { ...f.ended, timeZone: v } }))}
        style={{ width: '100%' }} />
      <p className={style.formFieldName}>
        <span>GeoLocation</span>
        <span
          style={{ cursor: 'pointer' }}
          onClick={() => getGeolocation().then(p => {
            setMemoryForm(f => ({ ...f, geoLocation: p }));
          })}></span>
      </p>
      <Input value={geoJson} onChange={v => {
        let parsed = v;
        try {
          parsed = JSON.parse(v);
        } catch (e) { }
        setMemoryForm(f => ({ ...f, geoLocation: parsed as any }));
      }} isTextArea style={{ width: '100%', height: 100, border: typeof memoryForm.geoLocation === 'string' ? '3px solid red' : undefined }} />
      <br /><br />
      <div style={{ float: 'left' }}>
        <Button disabled={hasError} onClick={() => onSave(memoryForm)}>Save</Button>
      </div>
      <div style={{ float: 'right' }}>
        <Button onClick={onCancel}>Cancel</Button>
      </div>
      {positionString ?
        <iframe
          width="100%"
          height="400"
          src={`https://maps.google.com/maps?width=520&height=400&hl=en&q=${positionString}&t=&z=12&ie=UTF8&iwloc=B&output=embed`}>
        </iframe> : null}
      <Button onClick={onDelete}>Delete </Button>
    </div>
  );
}

const formatMemoryStart = (memory: Memory) => {
  const today = new Date().toLocaleDateString('en', { timeZone: memory.started.timeZone });
  const memoryDate = new Date(memory.started.time);
  const memoryDay = memoryDate.toLocaleDateString('en', { timeZone: memory.started.timeZone });
  if (today === memoryDay) {
    return memoryDate.toLocaleTimeString('en', { timeZone: memory.started.timeZone });
  } else {
    return memoryDay;
  }
}

export const Memories = (props: MemoriesProps) => {
  const [position] = useAsync(getGeolocation, [], null);
  const { memoryHistory } = getLocalDB();
  const [isEditing, setIsEditing] = useState<Memory | null>(null);

  const render = useReRender();

  const addMemory = () => {
    const memory = createNewMemory();
    setIsEditing(memory);
  }

  const onDeleteMemory = () => {
    if (!isEditing?.id) return;
    const res = window.confirm(`Are you sure you want to delete the "${isEditing.title}" memory?`);
    if (!res) return;
    deleteMemory(isEditing.id);
    setIsEditing(null);
    render();
  }
  const onSaveMemory = (memory: Memory) => {
    if (!memory.id) addGeoToMemory(memory).then(() => {
      saveLocalDB();
      render();
    });
    saveMemory(memory);
    saveLocalDB();
    setIsEditing(null);
  }

  const uploadMemories = () => {
    memoryProvider.updateMemories(memoryHistory).then((res) => {
      Object.assign(memoryHistory, res);
      saveLocalDB();
      render();
    });
  };

  if (memoryHistory.lastUpdated - memoryHistory.serverVersion > 24 * 60 * 60 * 1000) {
    uploadMemories();
  }
  console.log(memoryHistory.lastUpdated - memoryHistory.serverVersion)

  return (
    <React.Fragment>
      <Header />
      <div style={{ textAlign: "center" }}>
        <h1>
          <span>Memories</span>
          <span onClick={uploadMemories} style={{
            color: memoryHistory.lastUpdated > memoryHistory.serverVersion ? '#f73a3a' : '#7faeff',
            cursor: 'pointer',
          }}></span>

        </h1>
        {isEditing ? (
          <MemoryForm memory={isEditing} onCancel={() => setIsEditing(null)} onSave={onSaveMemory} onDelete={onDeleteMemory} />
        ) : (
          <Button onClick={addMemory}>Add Memory</Button>
        )}
        <br /><br /><br />
        <table style={{ width: '100%', padding: 10, boxSizing: 'border-box', textAlign: 'left' }}>
          <thead>
            <tr>
              <th>Title</th>
              <th>Time</th>
              <th>Location</th>
            </tr>
          </thead>
          <tbody>
            {memoryHistory.memories.map((memory, i) => (
              <tr key={i} style={{ cursor: 'pointer', height: 30 }} onClick={() => setIsEditing(memory)}>
                <td>{memory.title}</td>
                <td>{formatMemoryStart(memory)}</td>
                <td>{
                  memory.geoLocation?.coords ?
                    position?.coords ?
                      calcCrow(
                        memory.geoLocation.coords.latitude,
                        memory.geoLocation.coords.longitude,
                        position.coords.latitude,
                        position.coords.longitude
                      ).toFixed(1) + ' km' :
                      ''
                    : ''
                }</td>
              </tr>
            ))}
          </tbody>
        </table>

      </div>
    </React.Fragment>
  );
}
