import { redirectToLogin } from "../components/loginRequired/loginRequired";
import config from "../config";
import authHelper from "../utils/authHelper";
import storageHelper from "../utils/storageHelper";

interface User {
  capabilities: string[];
  loggedIn: number;
  username: string;
}

let user: User | null = storageHelper.get('user');

class UserProvider {
  async get(): Promise<User> {
    const token = authHelper.getToken();
    if (token === null) throw new Error("UNAUTHORIZED");
    const res = await fetch(`${config("serverPath")}/api/user`, {
      headers: {
        Authorization: token
      }
    });
    if (res.status === 401) {
      redirectToLogin();
      throw new Error("UNAUTHORIZED");
    } else {
      const msg = await res.json();
      user = msg;
      storageHelper.set('user', msg);
      return msg;
    }
  }

  async getCached(): Promise<User> {
    if (user) return user;
    return (this || userProvider).get();
  }

  getCachedSync() {
    return user;
  }
}

const userProvider = new UserProvider();

export default userProvider;
export { UserProvider };
