import pushNotificationProvider from "../providers/pushNotificationProvider";

function urlBase64ToUint8Array(base64String: string) {
  var padding = '='.repeat((4 - base64String.length % 4) % 4);
  var base64 = (base64String + padding)
    .replace(/\-/g, '+')
    .replace(/_/g, '/');

  var rawData = window.atob(base64);
  var outputArray = new Uint8Array(rawData.length);

  for (var i = 0; i < rawData.length; ++i) {
    outputArray[i] = rawData.charCodeAt(i);
  }
  return outputArray;
}

const getKey = async () => {
  const vapidPublicKey = await pushNotificationProvider.getKey();
  const convertedVapidKey = urlBase64ToUint8Array(vapidPublicKey);
  return convertedVapidKey;
}


export const enablePushNotifications = async () => {
  const serviceWorkerRegistration = (window as any).serviceWorkerRegistration as ServiceWorkerRegistration;
  let pushSubscription = await serviceWorkerRegistration.pushManager.getSubscription();
  if (pushSubscription) {
    console.log('Already subscribed', pushSubscription);
    return;
  }

  const options: PushSubscriptionOptionsInit = {
    userVisibleOnly: true,
    applicationServerKey: await getKey(),
  };

  pushSubscription = await serviceWorkerRegistration.pushManager.subscribe(options);
  console.log(pushSubscription.endpoint);
  if (!pushSubscription) throw new Error('No push subscription');
  pushNotificationProvider.createPushSubscription(pushSubscription);

  // The push subscription details needed by the application
  // server are now available, and can be sent to it using,
  // for example, the fetch() API.


}