import { getLocalDB } from "../../utils/database";
import { deepCopy } from "../../utils/deepCopy";

export interface Memory {
  id: string;
  geoLocation: GeolocationPosition | null;
  title: string;
  description: string;
  url: string;
  tags: string[];
  started: {
    time: number;
    timeZone: string;
  };
  ended: {
    time: number;
    timeZone: string;
  };
  modificationTime: number;
}
export interface MemoryHistory {
  lastUpdated: number;
  serverVersion: number;
  memories: Memory[],
};

export const getUniqueId = () => `${new Date().getTime()}${Math.floor(Math.random() * 1000)}`;

let cachedGeoLocation: GeolocationPosition | null = null;
export const getGeolocation = async (): Promise<GeolocationPosition> => {
  return new Promise(resolve => {
    navigator.geolocation.getCurrentPosition(p => {
      // Geolocation on Android cannot be strigified, so we need to convert it to a plain object
      cachedGeoLocation = {
        coords: {
          latitude: p.coords.latitude,
          longitude: p.coords.longitude,
          accuracy: p.coords.accuracy,
          altitude: p.coords.altitude,
          altitudeAccuracy: p.coords.altitudeAccuracy,
          heading: p.coords.heading,
          speed: p.coords.speed,
        },
        timestamp: p.timestamp,
      };
      resolve(cachedGeoLocation);
    });
  })
}

const getTimeZone = () => Intl.DateTimeFormat().resolvedOptions().timeZone;


export const createNewMemory = () => {
  const newMemory: Memory = {
    id: '',
    geoLocation: cachedGeoLocation,
    title: '',
    description: '',
    url: '',
    tags: [],
    started: {
      time: Date.now(),
      timeZone: getTimeZone(),
    },
    ended: {
      time: Date.now(),
      timeZone: getTimeZone(),
    },
    modificationTime: 0,
  };
  return newMemory;
};

export const addGeoToMemory = async (memory: Memory) => {
  const geo = await getGeolocation();
  memory.geoLocation = geo;
}

export const deleteMemory = (id: string) => {
  const { memoryHistory } = getLocalDB();
  memoryHistory.memories.splice(memoryHistory.memories.findIndex(m => m.id === id), 1);
}
export const saveMemory = (memory: Memory) => {
  const { memoryHistory } = getLocalDB();
  if (!memory.id) memory.id = getUniqueId();
  memory.modificationTime = Date.now();
  const idx = memoryHistory.memories.findIndex(m => m.id === memory.id);
  if (idx !== -1) memoryHistory.memories.splice(idx, 1, memory);
  else memoryHistory.memories.unshift(memory);
}